import React from 'react';
import ReactDOM from 'react-dom';
import './main.css';

const arr = [
  ['a','あ','ア'],['i','い','イ'],['u','う','ウ'],['e','え','エ'],['o','お','オ'],
  ['ka','か','カ'],['ki','き','キ'],['ku','く','ク'],['ke','け','ケ'],['ko','こ','コ'],
  ['sa','さ','サ'],['shi','し','シ'],['su','す','ス'],['se','せ','セ'],['so','そ','ソ'],
  ['ta','た','タ'],['chi','ち','チ'],['tsu','つ','ツ'],['te','て','テ'],['to','と','ト'],
  ['na','な','ナ'],['ni','に','ニ'],['nu','ぬ','ヌ'],['ne','ね','ネ'],['no','の','ノ'],
  ['ha','ほ','ハ'],['hi','ひ','ヒ'],['fu','ふ','フ'],['he','へ','ヘ'],['ho','ほ','ホ'],
  ['ma','ま','マ'],['mi','み','ミ'],['mu','む','ム'],['me','め','メ'],['mo','も','モ'],
  ['ya','や','ヤ'],['yu','ゆ','ユ'],['yo','よ','ヨ'],
  ['ra','ら','ラ'],['ri','り','リ'],['ru','る','ル'],['re','れ','レ'],['ro','ろ','ロ'],
  ['wa','わ','ワ'],['o/wo','を','ヲ'],
  ['n','ん','ン'],
]

class Game extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      index: 0
    };
  }

  resetFun () {
    this.setState({
      index: 0
    })
  }

  prevFun () {
    let newIndex = this.state.index - 1
    if(newIndex < 0) {
      newIndex = arr.length - 1
    }
    this.setState({
      index: newIndex
    })
  }

  nextFun () {
    let newIndex = this.state.index + 1
    if(newIndex >= arr.length) {
      newIndex = 0
    }
    this.setState({
      index: newIndex
    })
  }

  render() {
    const jpWord = arr[this.state.index]

    return (
      <div>
        <div className="card">
          <div className="flex bg1">
            <div className="flex flex-column">
              <div className="word-top">{jpWord[0]}</div>
              <div className="word-left">{jpWord[1]}</div>
            </div>
            <div className="word-right">{jpWord[2]}</div>
          </div>
        </div>
        <div className="btns">
          <div onClick={() => this.nextFun()}>next</div>
          <div onClick={() => this.prevFun()}>prev</div>
          <div onClick={() => this.resetFun()}>reset</div>

        </div>
      </div>
    );
  }
}

// ========================================

ReactDOM.render(<Game />, document.getElementById("root"));